import { useState } from "react";
import Modal from "react-bootstrap/Modal";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import VisibilitySensor from "react-visibility-sensor";



const About = () => {
  const [showReadMore, setShowReadMore] = useState(false);

  function pronounce() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }

  let ReadMore = () => {
    return (
      <Modal
        show={showReadMore}
        onHide={() => {
          setShowReadMore(false);
        }}
        backdrop="true"
        keyboard={false}
        scrollable={true}
        contentClassName="modal-container"
        size="lg"
      >
        <Modal.Body className="modal-body">
          <div className="cert-container">
            <button
              className="close-btn"
              onClick={() => {
                setShowReadMore(false);
              }}
            >
              <i className="fa fa-times"></i>
            </button>
            

            <audio className="audio-element">
              <source src="/assets/myname.mp3"></source>
            </audio>
            <br />
            <h4 className="title">About me</h4>
            <p>
              My interest in development in general started when I discovered
              CMD, yes Windows command prompt. I wanted to hack a wifi password
              and I saw that i can use the command line for that (does not work)
              but turns out you can take a look at saved passwords that blew my
              mind. So I googled <b>“all cmd commands”</b> and started copying
              and pasting each one. I went through all of them and the next day
              when i open my laptop my windows crashed and it wouldn't open. So
              that made me realize to NEVER just copy and paste code into a
              command line. I took it to a repair shop and got it fixed and
              wanted to learn more.
              <br />
              <br />
              <h4 className="title">My first programming language</h4>
              The first thing I learned was how to run CMD commands with an
              executable file with batch programming. I started making CMD based
              applications like a file hider.
              <br />
              <br />
              After playing around with it for a while i had some ideas like a
              desktop cleanup script but i felt restricted and wanted to get
              into more advanced software development so i googled{" "}
              <b>“best programming language for beginners”</b> and python seemed
              like a good option because i’ve tried to learn C++ at one point
              and it seemed tedious and couldn't really figure it out{" "}
              <b>it wasn't as easy as batch programming</b> so i looked at
              python and it was awesome! Super simple to write and execute no
              need for compilers or anything. so i started taking a course on
              Youtube. I went through the course within the next 2-3 weeks, I
              learned about <b>Syntax</b>, <b>Comments</b>, <b>Data types</b>,
              <b>variables</b>, <b>Booleans</b>, <b>Operators</b> <b>,lists</b>{" "}
              <b>,if/else statements</b> <b>,Loops</b> and <b>dictionaries</b>.
              I went on to learn about some python modules and started working
              on more advanced projects.
              <br />
              My first project with python was my desktop cleanup script in
              which I learned that you can make <b>.exe</b> executable windows
              applications, modules, functions, classes, user inputs and more,
              after that i made simple command based applications calculators
              and what not.
              <br />
              <br />
              <h4 className="title">Web Devlopment</h4>
              I had experience with html and css from editing templates for fun
              but i didn't really know what i was doing at all actually, so i
              decided to learn Html and CSS, I discovered w3schools.com and got
              to work, after a few weeks i was pretty comfortable with both
              languages, after that i started to look for ways to make my site
              responsive and saw that bootstrap was a common choice didn't know
              about media queries back then i thought i knew CSS (i did but not
              the more advanced part of it) so i decided to make a portfolio
              page for my self which lead me to learning a lot of key concepts.
              I also wanted to learn about backend and storing data in databases
              so i got into PHP (didn't learn Javascript at this point) so i got
              the basics of PHP all my python skills applied to this as well so it was pretty
              easy to understand, With the help of google i was making CRUD web
              applications with HTML, CSS, PHP and mysql (No Javascript) i felt
              pretty confirmable in my basic web development skills and wanted
              to freelance.
              <br />
              <br />
              so i got on upwork, setup my profile and started applying for
              jobs. I got a few clients and started working on freelance
              projects, the websites i used to make were hideous so i got into
              design with Illustrator and vector graphics which led me to
              animation with adobe after effects, then i dabbled with both
              design and code for 2 years or so, after working on landing pages
              i wanted to work on web animation and some interactions like
              popups and and dynamic content so i saw that Jquery was the way to
              go but if i knew javascript that would be easier to understand so
              i started a course on javascript, and in a week i was okay with
              javascript a lot of the concepts from my experience with python it
              didn't take me as long as i thought it would to understand
              javascript, after getting the basics of it i felt like i could
              make anything i wanted.
              <br />
              <br />
              <h4 className="title">Now</h4>
              Fast forward 4 years later, I'm working on full stack web
              applications with Html, CSS3, React.js, Sass, Node.js, express.js,
              Mysql, firebase and a lot more technologies.
              <br />
              <br />
              Between those 4 years i’ve dabbled with app development with Ionic
              and then Flutter, flutter was better by a landslide i think it was
              because i understood javascript. I made a few apps with that and
              decided web dev was where my heart was so i’m currently working as
              a freelance software engineer and occasionally working on passion
              projects that i think are cool.
            </p>
            <div className="about-hi hor m-0">
             

              <a
                href="https://t.me/alemalohe"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-telegram "></i>
                </button>
              </a>

              <a
                href="https://twitter.com/alemalohe"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-twitter "></i>
                </button>
              </a>

              <a
                href="https://www.linkedin.com/in/alemalohe/"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-linkedin "></i>{" "}
                </button>
              </a>
            </div>
            <h3 className="title" hidden>
              Certificates
            </h3>
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1097/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1024/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1023/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1073/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1014/landscape/png"
              className="img-fluid cert"
              hidden
            />
          </div>
        </Modal.Body>
      </Modal>
    );
  };

  return (
    <div className="about">
      {showReadMore ? <ReadMore /> : ""}

      <div className="container">
        <div className="row skills">
          <div className="col-lg-12 text-center">
            

            <VisibilitySensor>
              {({ isVisible }) => {
                return (
                  <div
                    className={
                      isVisible ? "face p-3 hi-container" : "p-3 hi-container"
                    }
                  >
                    <img
                      src="assets/img/face.png"
                      width="180px"
                      style={{ borderRadius: "100%" }}
                      alt="https://alohe.github.io"
                    />
                    <div >
                      <img
                      hidden={isVisible ? false : true}
                        className="hi"
                        src="https://alohe.github.io/emojicloud/svg/Hand%20with%20fingers%20splayed.svg"
                      />
                    </div>
                  </div>
                );
              }}
            </VisibilitySensor>

            <h1 className="h1">About me</h1>
          </div>
          <div className="col-lg-4">
            <p className="h2">
              From custom design's to fully functional web applications, I can
              help you turn your personal or business concepts into exceptional
              website's that are robust, cost-effective, secure, scalable, and
              easily manageable.
              <br />
              <u className="text-dark">
                <a
                  data-toggle="collapse"
                  onClick={() => {
                    setShowReadMore(true);
                  }}
                  role="button"
                  aria-expanded="false"
                  aria-controls="More"
                  className="pointer"
                >
                  <button className="more-btn">More About Me</button>
                </a>
              </u>
            </p>
          </div>
          <div className="col-lg-8 skill-card-container">
            <p className="">
              Here are a few technologies I've been working with recently
            </p>
            <div className="skill-cards">
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-react"></i>
                </button>
                <h3 className="language">React</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-js"></i>
                </button>
                <h3 className="language">JavaScript (ES6+)</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-node-js"></i>
                </button>
                <h3 className="language">Node.js</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-bootstrap"></i>
                </button>
                <h3 className="language">Bootstrap</h3>
              </div>
              <div className="skill-card" hidden>
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/29/Postgresql_elephant.svg/1200px-Postgresql_elephant.svg.png"
                    alt=""
                  />
                </button>
                <h3 className="language">PostgreSQL</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://sass-lang.com/assets/img/logos/logo-b6e1ef6e.svg"
                    alt=""
                  />
                </button>
                <h3 className="language">Sass</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://www.kepler-dev.com/wp-content/uploads/2016/09/mysqllogo.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Mysql</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://cdn4.iconfinder.com/data/icons/google-i-o-2016/512/google_firebase-2-512.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Firebase</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://1000logos.net/wp-content/uploads/2020/06/Illustrator-Logo.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Illustrator</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://e1.pngegg.com/pngimages/188/872/png-clipart-macos-app-icons-framer-thumbnail.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Framer</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export { About };
